@use "abstracts" as *;

.nav {
  position: absolute;
  z-index: 10;
  left: -100%;

  display: grid;
  grid-template-rows: deep-map-get($header-height, lg) auto 1fr;
  grid-column: 1/2;
  grid-row: 1/-1;

  width: 100%;
  height: 100%;

  border-right: 2px solid var(--primary-color);
  background-color: var(--secondary-color);
  color: var(--primary-color);

  transition-property: left;
  transition-duration: map-get($transition-duration, collapse);

  @include sm {
    border-right: 0;
    grid-template-rows: deep-map-get($header-height, sm) auto 1fr;
  }

  &__list {
    a {
      @include text(sm);
      @include sm {
        @include text(base);
      }
    }
  }

  // 當 .nav 與 .nav-open 同時存在:
  &.nav-open {
    left: 0;

    // .nav 的兄弟元素 .main 內的 .hamburger--main 會被隱藏
    & + .main .hamburger--main {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__header {
    @include flex($ai: center, $jc: space-between);
    padding-left: 2.25rem;
    padding-right: 2.25rem;
    border-bottom: 2px solid var(--primary-color);
  }

  &__mode-btns {
    @include flex;
  }

  &__content {
    @include flex($dir: column, $jc: flex-start);
    height: calc(var(--app-height) - deep-map-get($header-height, lg));
    padding-bottom: 1rem;

    @include sm {
      height: calc(var(--app-height) - deep-map-get($header-height, sm));
    }
  }

  &__list-item {
    @include flex($ai: center);
    cursor: pointer;
    transition: background-color map-get($map: $transition-duration, $key: hover);

    // @ 「預設列表」的 icon 較窄，所以造成 nav 列表文字左右分配不均，
    // @ 所以有特別設定過它的距離。
    &:nth-child(2) {
      i {
        margin-left: 3px;
        width: 29px;
      }
    }

    @include hover-query {
      &:hover {
        background-color: var(--tertiary-color);
      }
    }
  }

  &__list-link {
    display: block;
    width: 100%;
    padding-top: 0.875rem;
    padding-bottom: 0.875rem;
    padding-left: 2.25rem;
    // padding-right: 2.25rem; // ! 右側應該是不用 padding
    color: var(--primary-color);

    // nav 內的每一個 list-item 在 $sm 的設定
    @include sm {
      padding-top: 0.575rem;
      padding-bottom: 0.575rem;
    }

    i {
      @include text(md);
      margin-right: 1.5rem;
      pointer-events: none;
      width: 2rem;
    }
  }

  &__list-link--custom-list {
    @include flex;
  }

  &__list-item--active {
    position: relative;
    background-color: var(--tertiary-color);

    &::before {
      content: "";
      position: absolute;
      right: 100%;
      top: 0;
      transform: translateX(100%);
      width: 10px;
      height: 100%;
      background-color: var(--primary-color);
      pointer-events: none;
    }
  }
}

.default-list {
  font-weight: 900;
  margin-top: 2rem;
}

.custom-list-title {
  @include text(md);
  @include flex($ai: center);
  margin-top: 2rem;
  white-space: nowrap;

  @include sm {
    margin-top: 1rem;
    @include text(sm);
  }

  &::before,
  &::after {
    content: "";
    display: block;

    width: 100%;
    height: 2px;

    background-color: var(--primary-color);
  }

  &::before {
    margin-right: 1.125rem;
  }

  &::after {
    margin-left: 1.125rem;
  }
}

.custom-list {
  margin-top: 2rem;
  margin-bottom: 2rem;
  overflow-y: scroll;

  @include sm {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  &__color {
    width: 2rem;
    height: 2rem;
    margin-right: 1.5rem;
    pointer-events: none;
    // flex-shrink: 0;
  }
}

.custom-list__name{
  // @ 文字省略(必須是 block or inline-block，必須有寬度)
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 14.0625rem; // 此為視窗大於 576px 時的文字寬度
  pointer-events: none; // 防止點按到此，點按到此會造成手機模式下點擊後自動收合 nav 的功能失效
}
